import Vue from 'vue'
import Router from 'vue-router'
import LandingPage from '@/components/landing-pages/LandingPage'
import PricingDetails from '@/components/pricing-details/Details'
import Products from '@/components/products/Ecommerce'
import WebProfile from '@/components/products/WebProfile'
import WebBisnis from '@/components/products/WebBisnis'
import WebSekolah from '@/components/products/WebSekolah'
import Blog from '@/components/products/Blog'
import Contact from '@/components/company/Contact'
import EasyOrder from '@/components/order/EasyOrder'
import ConfirmationOrder from '@/components/order/ConfirmationOrder'

Vue.use(Router)

export default new Router({
    mode: 'history',
    routes: [{
            path: '/',
            name: 'LandingPage',
            component: LandingPage
        },
        {
            path: '/pricing-details',
            name: 'Details',
            component: PricingDetails
        },
        {
            path: '/product/e-commerce',
            name: 'product',
            component: Products
        },
        {
            path: '/product/web-profile',
            name: 'web-profile',
            component: WebProfile
        },
        {
            path: '/product/web-bisnis',
            name: 'web-bisnis',
            component: WebBisnis
        },
        {
            path: '/product/web-sekolah',
            name: 'web-sekolah',
            component: WebSekolah
        },
        {
            path: '/product/blog',
            name: 'blog',
            component: Blog
        },
        {
            path: '/contact',
            name: 'contact',
            component: Contact
        },
        {
            path: '/order/step-order',
            name: 'step-order',
            component: EasyOrder
        },
        {
            path: '/order/confirmation',
            name: 'confirmation',
            component: ConfirmationOrder
        },
    ]
})


