<template>
<div>
      <!-- Latest posts section -->
    <section class="section is-medium" style="padding-top: 2rem;" id="blog">
        <div class="container">
            <!-- Title -->
            <div class="section-title-wrapper has-text-centered">
                <h3 class="section-title-landing">
                    Berita Terbaru
                </h3>
                <h4>
                    Dapatkan info dari artikel seputar produk dan layanan CyberDigital Indonesia
                </h4>
            </div>
            <!-- /Title -->

            <div class="content-wrapper">
                <div class="columns is-vcentered latest-posts">
                    <!-- Article card -->
                    <div class="column is-4" v-for="(blog, key) in news" :key="key">
                        <div class="card">
                            <div class="card-image">
                                <figure class="image is-4by3">
                                    <img :src="(blog.image)" alt="">
                                </figure>
                            </div>
                            <div class="card-content">
                                <div class="media">
                                    <div class="media-content">
                                        <p class="timestamp"><i class="sl sl-icon-clock"></i>{{ blog.time }}</p>
                                        <p class="title is-5">{{ blog.title }}</p>
                                    </div>
                                </div>

                                <div class="post-exerpt">
                                    <p>{{ blog.description }}</p>
                                </div>
                                <div class="has-text-centered">
                                    <a :href="(blog.link)" target="_blank" class="button button-cta is-bold btn-align primary-btn btn-outlined rounded">Baca Selengkapnya</a>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </section>
    <!-- /Latest posts section -->
</div>
</template>

<script>
const news = [
    {
        image: '/static/images/photos/posts/data-insight.jpg',
        title: 'Apa Perbedaan User Experience (UX) dan User Interface (UI)?',
        time: 'Mar 31 2018',
        description: 'Apa sih sebenarnya UX dan UI itu? Apakah kedua hal tersebut sama atau berbeda? Apa perbedaan&nbsp;antara dua hal tersebut? Kali ini kodi akan coba jelaskan secara singkat untuk kamu. Tolong koreksi kodi ya kalau ada penejelasan yang kurang cocok',
        link: '#',
    },
    {
        image: '/static/images/photos/posts/laptop-living.jpg',
        title: 'Alasan MacBook Jadi Laptop Terbaik Untuk Programming, Jangan Iri Ya!',
        time: 'Mar 31 2018',
        description: 'Mungkin ada sebagian dari kamu yang pernah merasa kenapa orang-orang hebat dan profesional selalu menggunakan MacBook dibandingkan laptop Windows lainnya.',
        link: '#',
    },
    {
        image: '/static/images/photos/posts/blog.jpg',
        title: 'Mau Fotomu Terlihat Photogenic dan Sempurna? Ini Triknya!',
        time: 'Mar 31 2018',
        description: 'Saat ini, media sosial sudah menjadi bagian dari hidup. Selainupdate status, kadang kamu juga akanupload foto. Nah, agar fotomu menarik dan mendapat banyaklikes, tentu kamu harus pasang foto yang bagus.',
        link: '#',
    },
];
export default {
    name: "LatestNews",
    data () {
        return {
            news,
        }
    }
}
</script>

